"use client";
import { useState } from "react";
import Link from "next/link";

export default function EquipoPage() {
  const [formData, setFormData] = useState({
    nombre: "",
    pais: "",
    director: "",
  });

  const [numeroEquipo, setNumeroEquipo] = useState(0);

  // Manejo del n√∫mero de equipo al cargar la p√°gina
  useEffect(() => {
    const ultimoNumero = localStorage.getItem("numeroEquipo") || 0; // Obtener el √∫ltimo n√∫mero de equipo
    const nuevoNumero = parseInt(ultimoNumero) + 1; // Incrementar en 1
    setNumeroEquipo(nuevoNumero); // Actualizar el estado
    localStorage.setItem("numeroEquipo", nuevoNumero); // Guardar el nuevo n√∫mero en localStorage
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Datos del equipo:", { ...formData, numeroEquipo });
    // Aqu√≠ puedes enviar los datos al servidor o realizar alguna acci√≥n
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-50 p-12">
      {/* Bot√≥n Volver al Home */}
      <Link href="/" className="absolute top-4 left-4 text-xl text-blue-500 hover:underline">
        Volver al Home
      </Link>

      <form
        onSubmit={handleSubmit}
        className="bg-gray-100 p-12 rounded-xl shadow-2xl max-w-5xl w-full space-y-10"
      >
        {/* N√∫mero de Equipo */}
        <div className="text-right">
          <h2 className="text-xl font-semibold text-black">
            N√∫mero de Equipo: <span className="font-bold">{numeroEquipo}</span>
          </h2>
        </div>

        {/* T√≠tulo */}
        <h1 className="text-4xl font-bold text-center text-black">
          Alta de Equipo
        </h1>

        {/* Nombre y Pa√≠s */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-10">
          <div>
            <label
              className="block text-lg font-medium mb-3 text-black"
              htmlFor="nombre"
            >
              Nombre
            </label>
            <input
              type="text"
              id="nombre"
              name="nombre"
              placeholder="Nombre del equipo"
              value={formData.nombre}
              onChange={handleInputChange}
              className="w-full p-4 border rounded-lg shadow-sm bg-white outline-none"
            />
          </div>

          <div>
            <label
              className="block text-lg font-medium mb-3 text-black"
              htmlFor="pais"
            >
              Pa√≠s
            </label>
            <div className="flex items-center border rounded-lg shadow-sm bg-white">
              <input
                type="text"
                id="pais"
                name="pais"
                placeholder="Selecciona el pa√≠s"
                value={formData.pais}
                onChange={handleInputChange}
                className="flex-1 px-4 py-3 outline-none text-black"
              />
              <button
                type="button"
                className="px-4 py-3 bg-gray-200 rounded-r-lg hover:bg-gray-300"
                onClick={() => alert("Agregar pa√≠s")}
              >
                +
              </button>
            </div>
          </div>
        </div>

        {/* Director */}
        <div>
          <label
            className="block text-lg font-medium mb-3 text-black"
            htmlFor="director"
          >
            Director
          </label>
          <div className="flex items-center border rounded-lg shadow-sm bg-white">
            <input
              type="text"
              id="director"
              name="director"
              placeholder="Buscar director"
              value={formData.director}
              onChange={handleInputChange}
              className="flex-1 px-4 py-3 outline-none text-black"
            />
            <button
              type="button"
              className="px-4 py-3 bg-gray-200 rounded-r-lg hover:bg-gray-300"
            >
              üîç
            </button>
          </div>
        </div>

        {/* Botones */}
        <div className="flex justify-end space-x-6">
          <button
            type="button"
            className="px-8 py-4 bg-gray-200 text-black rounded-lg shadow hover:bg-gray-300"
            onClick={() => alert("Cancelar")}
          >
            Cancelar
          </button>
          <button
            type="submit"
            className="px-8 py-4 bg-blue-500 text-white rounded-lg shadow hover:bg-blue-600"
          >
            Aceptar
          </button>
        </div>
      </form>
    </div>
  );
}
